import React, { PureComponent } from "react";
class CTAButton extends PureComponent {
  render() {
    const { cta, ctaClass, postCta, onClickToCTA, item } = this.props;
    const postCtaStatus = postCta && postCta.status === 1 ? true : false
    const ctaStyle = {
      color: postCtaStatus ? postCta.color : cta.color,
      backgroundColor: postCtaStatus ? postCta.background : cta.background
    }

    return <div className={ctaClass} style={ctaStyle} role="button" tabIndex="0" aria-label="CTA Button"
      onClick={
        (e) => {
          e.stopPropagation();
          onClickToCTA(item)
          window.open(
            `${postCtaStatus ? postCta.url : cta.url}`,
            '_blank')
        }}
    > {postCtaStatus ? postCta.text : cta.text}</div>
  }
}


export default (CTAButton);